.img3{
    width: 100px;
    height: 100px;
}
.img2{
    width: 500px;
    height: 100px;
    padding: 20px;
    
}
.Lap_h{
    color:#5151a2;
    padding-left: 100px;
    font-size: 60px;
    
}
.wrap_contacts {
    display: flex;
    justify-content: space-between;
    height: 100px;
    width: 400px;
    display: flex;
   
}
.tel {
    color:rgb(81, 81, 162);
    align-self: center;
    
    font-size: 25px;
    letter-spacing: 4px;
    align-self: center;
    
}
.header{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 200px;
    padding-right: 50px;
}
.logo2{
    display: flex;
    flex-direction: column;
    height: 200px;
}

@media screen and (max-width: 436px){
    .header {
        flex-direction: column;
        height: 180px;
        justify-content: baseline;
        width: 100%;
        padding: 0px;
    }
    
    .Lap_h {
        font-size: 30px;
    letter-spacing: 15px;
        padding: 0px;
       text-align: center;
       border-bottom: 1px solid;
       padding-bottom: 10px;
       padding-top: 10px;
       background-color: #5151a2;
       color: white;
       margin: 0px;
    }
    .wrap_contacts {
        padding: 0px 30px 0px 20px;
        justify-content: space-around;
        height: 50px;
    }
    .tel {
        font-size: 20px;
    }
    .img3{
        width: 50px;
        height: 26px;
        align-self: center;
    }
    .img2{
        width: 500px;
        height: 50px;
        padding: 10px;
        align-self: center;
    }
    .logo2{
       
        height: 100px;
    }
    .header {
        height: 160px;
        justify-content:flex-start ;
    }
}