}
.wrap_home{
    display: flex;
    flex-direction: column;
}
.wrap_c_h{
    display: flex;
    padding: 15px;
    align-self: center;
}
.wrap_card_h {
    margin-top: 50px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    width: 300px;
    
   
}
.exp{
    width: 150px;
    border: 4px solid #5151a2;
    padding: 28px;
    border-radius: 100px;
    align-self: center;
    
}
.dos{
    width: 150px;
    border: 4px solid #5151a2;
    padding: 28px;
    border-radius: 100px;
    align-self: center;
}
.exp2{
    width: 150px;
    border: 4px solid #5151a2;
    padding: 18px;
    border-radius: 100px;
    align-self: center;
    
}
p{
    color:#3f3f7a;
    text-align: center;
    font-size: 25px;

}
.logo{
    width: 525px;
    overflow: hidden;
    border-radius: 40px;
    align-self: center;
    width: 60%;
}
.us {
    padding: 20px;
    font-size: 30px;
}
@media screen and (max-width: 436px){
    .wrap_c_h {
        flex-direction: column;
    }
    .logo {
        width: 90%;
    }
}