 .bt2{
    text-align: center;
 }

.wrap_card {
    width: 300px;
    padding: 25px;
    height: 500px;
    display: flex;
    border-radius: 20px;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 50px;
    margin-left: 30px;
    box-shadow: 5px 5px 15px #5151a2;
}  
.img{
        width: 100%;
       /* // height: 300px; */
    }
.wrap_card_img{
    height:300px;
    display:flex;
    flex-direction: column;
    justify-content: center;
}



.btn {
    background-color: rgb(81, 81, 162);
    color:white;
    padding: 15px;
    width: 70%;
    border-radius: 10px;
    align-self: center;
    font-size: 25px;
    font-family: 'label';
    margin-bottom: 10px;
    margin-top: 15px;
    text-align: center;
}
    
  
.name{
text-align: center;
font-family: 'label';
font-size: 20px;

padding: 0px 15px 0px 15px;
}
.prise {
    font-family: 'label';
    text-align: center;
    font-size: 25px;
    
}

@media screen and (max-width: 476px) {
    .wrap_card {
        margin-left: 0px;
    }
}