h1{
    text-align: center;
    color:#5151a2;
}

.wrap_pr_img{
    width: 50%;
    
    display: flex;
    margin: 10px auto;
}
.img1{
    width: 50%;
    height: 100%;
}
.wrap_pr{
    display: flex;
    flex-direction: column;
}
.spec{
    display: flex;
    width: 70%;
    margin: 10px auto;
}
.span1{
    font-size: 20px;
    margin-right: 5px;
}
.span2{
    flex-grow: 1;
    border-bottom: 3px dotted  grey;
    margin-bottom: 5px;
}
.span3{
    font-size: 20px;
    margin-left: 5px;
}
.bt {
    letter-spacing: 10px;
    font-size: 40px;
}

@media screen and (max-width: 476px) {
    img{
        width: 100%;
    }
    .span2 {
        display: none;
    }
    .spec {
        justify-content: space-between;
        border-bottom: 1px dashed;
    }
    .span1 {
        width: 183px;
    }
    .spec{
        justify-content: space-between;
    }
    .span3 {
        align-items: end;
        text-align: end;
    }
}