}
.footer{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 200px;
    padding-right: 50px;
    background-color: rgb(81, 81, 162);
    margin-top: 40px;
    
}
.logo2{
    display: flex;
    flex-direction: column;
    align-self:center;
}
.Lap_h2{
    color: white;
    padding-left: 100px;
    font-size: 60px;
}

@media screen and (max-width: 436px) {
    .footer {
        margin-top: 30px;
        flex-direction: column;
        height: 200px;
        justify-content: center;
        padding: 50px;
    }
    
    
    .Lap_h2{
        font-size: 30px;
