.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    opacity: 0;
    pointer-events: none;

}

.overlay {
    width: 100%;
    height: 100%;
    background: rgba(111, 116, 111, 0.518);
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    padding: 20px;
    border-radius: 15px;
    background-color: rgb(81, 81, 162);
    color: white;
    
    transform: scale(0.3);
    transition: 0.3s transform ;
    display: flex;
    flex-direction: column;
    width: 700px;
    

}

.btn2{
    height: 50px;
    margin: 30px;
    background-color: rgb(81, 81, 162);
    color: white;
    font-size: 25px;
    border: 1px solid white;
    border-radius: 10px;
}
.input{
    margin: 30px;
    padding: 20px;
    height: 50px;
    font-size: 30px;
    border-radius: 10px;
    
}
.content1{
    display: flex;
    flex-direction: column;
}
.h3{
    text-align: center;
    font-size: 30px;
}
.opened {
    pointer-events: auto;
    opacity: 1;
    z-index: 10;
    transition: 0.3s transform ;
    transform: scale(1);
        .content{
        transform: scale(1);
    }
}
.opened2 {
    transition: 0.3s transform ;
    transform: scale(1)
}


.opened.content{
    transform: scale(1);
    transition: 0.3s transform ;
}



.isClosed {
    transition: 0.3s transform ;
    .content{
        transform: scale(0);
    }
}
.none {
    display: none;
}

.contant3{
    
    width: 650px;
    height: 650px;
    
}
.content4{
    width: 600px;
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    overscroll-behavior-x: contain;
    scroll-snap-type: x mandatory;
}
.img_modal{
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
}
.chek {
    font-size: 50px;
    text-align: center;
    padding: 50px;
}

@media screen and (max-width: 476px) {
    .content4 {
        width: 430px;
        height: 430px;
        
   
    }
    .contant3{
    
        width: 430px;
        height: 430px;
        
    }
    .chek {
        font-size: 40px;
        text-align: center;
        padding: 20px;
    }

}




